import { PolygonNode } from './polygon-node';

export class SquareNode extends PolygonNode {

  constructor(x, y) {
    super(x, y);
    this.generateDefaultTextLocation();
    this.inscribed = [ 45, 135, 225, 315 ];
    this.separation = 90;
  }

}



